/** FONTS **/
/** COLORS **/
html {
  font-size: 100% !important; }

body {
  line-height: 1.96rem !important;
  font-size: 1.4rem !important;
  font-weight: 400 !important; }

.home #main {
  padding-top: 0; }

html.avada-is-100-percent-template #main.width-100 {
  padding-left: 0 !important;
  padding-right: 0 !important; }

.post-content h3,
h3 {
  letter-spacing: -0.01em !important; }

/** BANNER **/
.fusion-header .fusion-logo {
  margin-top: 0 !important; }

.fusion-header-v4 .fusion-header {
  padding: 20px !important; }

/** MAIN MENU **/
header .fusion-secondary-main-menu {
  background-color: #3C5EA0; }

.avada-menu-highlight-style-bottombar.fusion-top-header:not(.fusion-header-layout-v6) .fusion-main-menu > ul > li > .fusion-bottombar-highlight::after {
  border-top: #00B1FC 8px solid !important; }

body:not(.fusion-header-layout-v6).fusion-top-header .fusion-main-menu > ul > li:not(.fusion-menu-item-button) > a.fusion-bottombar-highlight:not(.fusion-main-menu-icon)::after {
  top: calc(50% + 0.9em) !important; }

/** HOMEPAGE **/
.fusion-title.homepage-title h1 {
  font-size: 2.2em;
  line-height: 3.528rem !important;
  color: #3C5EA0;
  font-family: "depot-new-condensed-web";
  font-weight: 500; }

.homepage-contentboxes .col-lg-4 {
  padding-left: 30px;
  padding-right: 30px; }

.homepage-contentboxes .content-box-wrapper {
  min-height: 450px;
  border: #00B1FC 1px solid; }
  .homepage-contentboxes .content-box-wrapper .heading {
    background-color: #00B1FC;
    padding: 25px 0; }
    .homepage-contentboxes .content-box-wrapper .heading .heading-link {
      float: none !important; }
    .homepage-contentboxes .content-box-wrapper .heading .content-box-heading {
      font-size: 1.2em !important;
      text-transform: uppercase;
      color: #fff;
      text-align: center; }
  .homepage-contentboxes .content-box-wrapper .content-container,
  .homepage-contentboxes .content-box-wrapper .fusion-read-more-button {
    margin-left: 10%;
    margin-right: 10%; }
  .homepage-contentboxes .content-box-wrapper .content-container {
    color: #3C5EA0 !important; }

.homepage-contentboxes .button-default {
  background-color: #fff;
  border-radius: unset;
  border: #C35B2A 1px solid; }
  .homepage-contentboxes .button-default .fusion-button-text {
    color: #C35B2A;
    font-family: "depot-new-condensed-web";
    font-size: 1.9em;
    font-weight: 500; }
